import * as GitHubApiCreateCommit from "../gith/index";

interface FormElements extends HTMLFormControlsCollection {
    username: HTMLInputElement;
    repository: HTMLInputElement;
    "access-token": HTMLInputElement;
    "base-branch": HTMLInputElement;
    "head-branch": HTMLInputElement;
    "commit-message": HTMLInputElement;
    "file-path": HTMLInputElement;
    "file-content": HTMLTextAreaElement;
}

interface CommitFormElement extends HTMLFormElement {
    readonly elements: FormElements;
}

class GitHubCommitApp {
    private form: CommitFormElement;
    private submitButton: HTMLButtonElement;
    private resultMessage: HTMLElement;

    constructor() {
        this.form = document.getElementById('commit-form') as CommitFormElement;
        this.submitButton = this.form.querySelector('.commit-button') as HTMLButtonElement;
        this.resultMessage = document.getElementById('result-message') as HTMLElement;
        
        this.init();
    }

    private init(): void {
        this.form.addEventListener('submit', this.handleSubmit.bind(this));
        
        // Add input event listeners for real-time validation
        const inputs = this.form.querySelectorAll('input, textarea');
        inputs.forEach(input => {
            input.addEventListener('input', this.validateForm.bind(this));
        });
        
        // Initial validation
        this.validateForm();
    }

    private validateForm(): boolean {
        const elements = this.form.elements;
        let isValid = true;

        // Check if all required fields are filled
        const requiredFields = [
            elements.username,
            elements.repository,
            elements['access-token'],
            elements['base-branch'],
            elements['head-branch'],
            elements['commit-message'],
            elements['file-path'],
            elements['file-content']
        ];

        for (const field of requiredFields) {
            if (!field.value.trim()) {
                isValid = false;
                break;
            }
        }

        // Validate file path format
        const filePath = elements['file-path'].value.trim();
        if (filePath && !this.isValidFilePath(filePath)) {
            isValid = false;
        }

        // Validate branch names
        const baseBranch = elements['base-branch'].value.trim();
        const headBranch = elements['head-branch'].value.trim();
        if (baseBranch && headBranch && !this.isValidBranchName(baseBranch)) {
            isValid = false;
        }
        if (baseBranch && headBranch && !this.isValidBranchName(headBranch)) {
            isValid = false;
        }

        this.submitButton.disabled = !isValid;
        return isValid;
    }

    private isValidFilePath(path: string): boolean {
        // Basic file path validation
        const pathRegex = /^[a-zA-Z0-9._\-\/]+\.[a-zA-Z0-9]+$/;
        return pathRegex.test(path) && !path.includes('..') && !path.startsWith('/');
    }

    private isValidBranchName(branch: string): boolean {
        // Git branch name validation rules
        const branchRegex = /^[a-zA-Z0-9._\-\/]+$/;
        return branchRegex.test(branch) && 
               !branch.startsWith('.') && 
               !branch.endsWith('.') &&
               !branch.includes('..') &&
               !branch.includes(' ');
    }

    private async handleSubmit(event: Event): Promise<void> {
        event.preventDefault();
        
        if (!this.validateForm()) {
            this.showMessage('Please fill in all required fields correctly.', 'error');
            return;
        }

        const elements = this.form.elements;
        
        // Show loading state
        this.setLoadingState(true);
        this.hideMessage();

        try {
            const github = GitHubApiCreateCommit.create({
                owner: elements.username.value.trim(),
                repo: elements.repository.value.trim(),
                accessToken: elements['access-token'].value.trim(),
            });

            await github.createGitCommit({
                baseBranchName: elements['base-branch'].value.trim(),
                headBranchName: elements['head-branch'].value.trim(),
                commitMessage: elements['commit-message'].value.trim(),
                files: [
                    {
                        path: elements['file-path'].value.trim(),
                        content: elements['file-content'].value,
                    },
                ],
            });

            this.showMessage(
                `Successfully created commit on branch "${elements['head-branch'].value.trim()}"!`, 
                'success'
            );
            
            // Optionally clear the form after successful commit
            // this.clearForm();
            
        } catch (error) {
            console.error('Commit failed:', error);
            
            let errorMessage = 'Failed to create commit. Please check your credentials and try again.';
            
            if (error instanceof Error) {
                // Handle specific error cases
                if (error.message.includes('401')) {
                    errorMessage = 'Authentication failed. Please check your access token.';
                } else if (error.message.includes('404')) {
                    errorMessage = 'Repository not found. Please check the username and repository name.';
                } else if (error.message.includes('403')) {
                    errorMessage = 'Permission denied. Make sure your access token has the required permissions.';
                } else if (error.message.includes('422')) {
                    errorMessage = 'Invalid request. Please check your branch names and file path.';
                } else {
                    errorMessage = `Error: ${error.message}`;
                }
            }
            
            this.showMessage(errorMessage, 'error');
        } finally {
            this.setLoadingState(false);
        }
    }

    private setLoadingState(loading: boolean): void {
        if (loading) {
            this.submitButton.classList.add('loading');
            this.submitButton.disabled = true;
        } else {
            this.submitButton.classList.remove('loading');
            this.submitButton.disabled = !this.validateForm();
        }
    }

    private showMessage(message: string, type: 'success' | 'error' | 'info'): void {
        this.resultMessage.textContent = message;
        this.resultMessage.className = `result-message ${type} show`;
        
        // Auto-hide success messages after 5 seconds
        if (type === 'success') {
            setTimeout(() => {
                this.hideMessage();
            }, 5000);
        }
    }

    private hideMessage(): void {
        this.resultMessage.classList.remove('show');
    }

    private clearForm(): void {
        this.form.reset();
        this.validateForm();
        this.hideMessage();
    }
}

// Initialize the app when DOM is loaded
document.addEventListener('DOMContentLoaded', () => {
    new GitHubCommitApp();
});

// Export for potential external use
export default GitHubCommitApp;